import React from "react";

import { ToolbarButton } from "./toolbarbutton";

export const Toolbar = ()=>{

    return (
      <div className="collapse navbar-collapse" id="navbarCollapse">
        <ul className="ml-auto menu">
          <ToolbarButton linkRef="/" text="HOME">
            <i className="fas fa-home icon-large" />
          </ToolbarButton>
          <ToolbarButton
            linkRef="/docs/volunteer.docx"
            newTab="true"
            text="VOLUNTEER"
          >
            <i className="fas fa-hands-helping icon-large" />
          </ToolbarButton>
          <ToolbarButton
            linkRef="https://rehome.adoptapet.com/"
            newTab="true"
            text="REHOME"
          >
            <i className="fas fa-heart-broken icon-large" />
          </ToolbarButton>
          <ToolbarButton newTab="true" linkRef="https://www.petco.com">
            <img
              alt=""
              className="menu-button-icon"
              src="https://www.petco.com/wcsstore/PetcoStore/img/brand-logo.png"
            />
          </ToolbarButton>
          <ToolbarButton linkRef="/faq" text="FAQ">
            <i className="fas fa-question icon-large" />
          </ToolbarButton>
        </ul>
      </div>
    );
}

